import { Table, message, Input, Space, Button } from "antd";
import { useEffect, useState } from "react";
import Highlighter from "react-highlight-words";
import copy from "copy-to-clipboard";
import {
  CaretDownOutlined,
  CaretRightOutlined,
  SearchOutlined,
} from "@ant-design/icons";
import Select from "rc-select";
var json = require("../data/kadastr_karabi.json");

const WorkPage = () => {
  const { Option } = Select;
  // C—á–µ—Ç—á–∏–∫ –ø–µ—â–µ—Ä
  const [cavesCount, setCavesCount] = useState(0);

  // –ü–æ–∏—Å–∫ –ø–æ –∫–æ–ª–æ–Ω–∫–µ
  const [searchText, setSearchText] = useState("");
  const [searchedColumn, setSearchedColumn] = useState("");
  let searchInput;

  const getColumnSearchProps = (dataIndex) => ({
    filterDropdown: ({
      setSelectedKeys,
      selectedKeys,
      confirm,
      clearFilters,
    }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={(node) => {
            searchInput = node;
          }}
          placeholder={`–í–≤–µ–¥–∏—Ç–µ –∑–∞–ø—Ä–æ—Å`}
          value={selectedKeys[0]}
          onChange={(e) =>
            setSelectedKeys(e.target.value ? [e.target.value] : [])
          }
          onPressEnter={() => handleSearch(selectedKeys, confirm, dataIndex)}
          style={{ marginBottom: 8, display: "block" }}
        />
        <Space>
          <Button
            type="primary"
            onClick={() => handleSearch(selectedKeys, confirm, dataIndex)}
            icon={<SearchOutlined />}
            size="small"
            style={{ width: 90 }}
          >
            –ü–æ–∏—Å–∫
          </Button>
          <Button
            onClick={() => {
              handleReset(clearFilters);
              handleSearch(selectedKeys, confirm, dataIndex);
              handleReset(clearFilters);
            }}
            size="small"
            style={{ width: 90 }}
          >
            –°–±—Ä–æ—Å
          </Button>
          <Button
            type="link"
            size="small"
            onClick={() => {
              confirm({ closeDropdown: false });
              setSearchText(selectedKeys[0]);
              setSearchedColumn(dataIndex);
            }}
          >
            -
          </Button>
        </Space>
      </div>
    ),
    filterIcon: (filtered) => (
      <SearchOutlined style={{ color: filtered ? "#1890ff" : undefined }} />
    ),
    onFilter: (value, record) =>
      record[dataIndex]
        ? record[dataIndex]
            .toString()
            .toLowerCase()
            .includes(value.toLowerCase())
        : "",
    onFilterDropdownVisibleChange: (visible) => {
      if (visible) {
        setTimeout(() => searchInput.select(), 100);
      }
    },
    render: (text) =>
      searchedColumn === dataIndex ? (
        <Highlighter
          highlightStyle={{ backgroundColor: "#ffc069", padding: 0 }}
          searchWords={[searchText]}
          autoEscape
          textToHighlight={text ? text.toString() : ""}
        />
      ) : (
        text
      ),
  });

  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();

    setSearchText(selectedKeys[0]);
    setSearchedColumn(dataIndex);
  };

  const handleReset = (clearFilters) => {
    clearFilters();
    setSearchText("");
  };

  // –ö–æ–Ω–µ—Ü –ø–æ–∏—Å–∫–∞ –ø–æ –∫–æ–ª–æ–Ω–∫–µ

  const columns = [
    {
      title: "–ù–∞–∑–≤–∞–Ω–∏–µ",
      dataIndex: "cave_name",
      ...getColumnSearchProps("cave_name"),
      // fixed: "left",
      // render: (record) => (
      //   <>
      //     <b>{record.cave_name} </b>
      //     <br />
      //     <font color="red">‚ñº</font> {record.deep}{" "}
      //     <span>
      //       <font color="green">‚û§</font>{" "}
      //     </span>{" "}
      //     {record.length}
      //   </>
      // ),
      key: "name",
    },
    {
      title: (
        <font color="red">
          <CaretDownOutlined />
        </font>
      ),
      dataIndex: "deep",
      key: "deep",
      sorter: (a, b) => a.deep - b.deep,
    },
    {
      title: (
        <font color="green">
          <CaretRightOutlined />
        </font>
      ),
      dataIndex: "length",
      key: "length",
      sorter: (a, b) => a.length - b.length,
    },

    {
      title: "–ö–æ–æ—Ä–¥–∏–Ω–∞—Ç—ã",
      key: "location",
      render: (record) => (
        <a
          onClick={() => {
            copy(record.location);
            message.success("–ö–æ–æ—Ä–¥–∏–Ω–∞—Ç—ã —Å–∫–æ–ø–∏—Ä–æ–≤–∞–Ω—ã");
          }}
        >
          {record.location}
        </a>
      ),
      // fixed: "right",
    },
  ];

  useEffect(() => {
    setCavesCount(json.length);
    console.log(cavesCount);
  }, []);

  return (
    <div>
      <div className="alphaback" style={{ padding: "0 15px" }}>
        <span>
          <Space>
            C–ø–µ–ª–µ–æ—Ä–∞–π–æ–Ω:
            <select>
              <option key={0} value="–ö–∞—Ä–∞–±–∏–π—Å–∫–∏–π">
                –ö–∞—Ä–∞–±–∏–π—Å–∫–∏–π
              </option>
              <option key={1} value="–ê–π-–ü–µ—Ç—Ä–∏–Ω—Å–∫–∏–π">
                –ê–π-–ü–µ—Ç—Ä–∏–Ω—Å–∫–∏–π
              </option>
            </select>
          </Space>
        </span>
      </div>
      <div className="alphaback" style={{ padding: "0 15px" }}>
        <i>–í—Å–µ–≥–æ –ø–µ—â–µ—Ä –≤ –∫–∞—Ç–∞–ª–æ–≥–µ: </i>{" "}
        <span>
          {" "}
          <i>
            <b>{cavesCount}</b>
          </i>
        </span>{" "}
      </div>
      <Table
        dataSource={json}
        columns={columns}
        pagination={{ position: ["none", "bottomCenter"] }}
      ></Table>
    </div>
  );
};

export default WorkPage;
